:root {
  --violet-hocarre: rgb(76, 2, 62);
  --beige: rgb(237, 213, 183);
  --light-blue: rgb(93, 202, 209);
  --electric-blue: rgb(69, 104, 255);
  --white: rgb(250, 250, 250);
}

:root {
  --base-font: "Roboto", sans-serif;
}

/*---------------------------------------------------
* RESETS AND BOILEPLATE STYLE
-----------------------------------------------------*/
* {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0px;
  margin: 0px;
  font-family: var(--base-font);
  background-color: var(--white);
  line-height: 1.1;
  height: 100vh;
  scroll-behavior: smooth;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

/*
** RIPPLE EFFECT FOR ALL THE BUTTONS
*/
span.ripple {
  position: absolute;
  transform: scale(0);
  animation: ripple 600ms linear;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.7);
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}
/*-----------------------------
*GLOBAL NAVBAR STARTS
 ------------------------------ */
.header {
  width: 100%;
  max-height: 70px;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem 1.5rem;
  padding: 0;
  width: 100%;
  max-height: 70px;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: var(--white);
  box-shadow: 0px 0px 10px gray;
}

.navbar__logo {
  position: relative;
  left: 3.4375rem;
}

.logo {
  width: 11.875rem;
  height: 100%;
}

.menu__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu__item {
  margin: 0 2.1875rem;
}

.item__link {
  font-size: 1.125rem;
  font-weight: 700;
  color: black;
  padding-bottom: 10px;
  position: relative;
}

/* ====> hover effect -> underlined links */
.item__link.underlined:hover {
  color: var(--violet-hocarre);
}

.item__link.underlined::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 0.125rem;
  background-color: var(--violet-hocarre);
  transform: scaleX(0);
  transition: transform 0.4s ease;
}

.item__link.underlined:hover::before {
  transform: scaleX(1);
}

/* ====> signUp CTA button*/
button {
  outline: 0;
  position: relative;
  overflow: hidden;
  transition: background 400ms;
}

.sign-up-btn {
  border: 0.1875rem solid var(--violet-hocarre);
  color: var(--violet-hocarre);
  background-color: transparent;
  border-radius: 0.3125rem;
  padding: 0.3125rem 1.5625rem;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.3);
  font-weight: 700;
  cursor: pointer;
}

.sign-up-btn:hover {
  transition: 0.3s ease-out;
  background-color: var(--violet-hocarre);
  color: var(--white);
}

/* ------------------------------------
*GLOBAL NAVBAR ENDS
--------------------------------  */
/*--------------------------------
*HAMBURGER MENU STARTS
--------------------------------- */
.menu__hamburger {
  border: none;
  background-color: var(--white);
}

.menu__hamburger {
  display: none;
}

.menu__bar {
  display: block;
  width: 1.5625rem;
  height: 0.1875rem;
  margin: 0.3125rem auto;
  transition: all 0.3s ease-in-out;
  background-color: var(--violet-hocarre);
}

/*----------------------------------
*HAMBURGER MENU ENDS
-----------------------------------  */
/*----------------------------------
*MOBILE VERSION STARTS
-----------------------------------  */
@media only screen and (max-width: 54.0625rem) {
  .navbar {
    justify-content: space-between;
  }

  .menu__list {
    position: fixed;
    left: -1000%;
    top: 3.4375rem;
    flex-direction: column;
    width: 100%;
    border-radius: 0.625rem;
    text-align: center;
    transition: 0.7s;
    background-color: var(--white);
  }

  .menu__list.menu--is-active {
    left: 0;
  }

  .menu__item {
    margin: 1.5625rem 0;
  }

  .menu__hamburger {
    display: block;
    cursor: pointer;
    margin-right: 1.875rem;
    justify-content: flex-end;
  }

  .menu__hamburger.menu--is-active .menu__bar:nth-child(2) {
    opacity: 0;
  }

  .menu__hamburger.menu--is-active .menu__bar:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }

  .menu__hamburger.menu--is-active .menu__bar:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }
}
/*----------------------------------
*HAMBURGER VERSION ENDS
-----------------------------------  */
.section-presentation {
  height: 100vh;
  margin-top: 70px;
  background-color: var(--beige);
  color: var(--violet-hocarre);
}

.presentation-container {
  width: 100%;
  height: 100%;
  display: flex;
}

/*------------------------------------------------------------------------
* LEFT PART STARTS
--------------------------------------------------------------------------*/
.presentation__left {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.presentation__header,
.presentation__body,
.presentation-cta {
  width: 80%;
}

.presentation__header {
  font-size: 2.8125rem;
  font-weight: 700;
  margin-top: 1.875rem;
}

.presentation-title,
.presentation-text {
  margin: 0.9375rem 0 0.9375rem 1.875rem;
}

.presentation__body {
  font-size: 1.4375rem;
  margin-bottom: 1.875rem;
}

.presentation-cta__button {
  border: none;
  background-color: var(--violet-hocarre);
  color: #fff;
  border-radius: 0.4375rem;
  padding: 0.625rem;
  font-weight: 600;
  cursor: pointer;
  position: relative;
  left: 5%;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.4);
}

/*------------------------------------------------------------------------
* LEFT PART ENDS
--------------------------------------------------------------------------*/
/*------------------------------------------------------------------------
* RIGHT PART STARTS
--------------------------------------------------------------------------*/
.presentation__right {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
}

/*------------------------------------------------------------------------
* RIGHT PART ENDS
--------------------------------------------------------------------------*/
@media screen and (max-width: 56.5625rem) {
  .presentation-container {
    flex-direction: column-reverse;
    align-items: flex-start;
  }

  .presentation__left {
    height: 55%;
    width: 100vw;
    justify-content: flex-start;
  }

  .presentation__right {
    height: 45%;
    width: 100%;
    align-items: flex-start;
  }

  .presentation__right img {
    height: 80%;
    width: 80%;
  }

  .presentation__header {
    font-size: 1.875rem;
    width: 70%;
  }

  .presentation__body {
    font-size: 1.125rem;
    width: 70%;
  }

  .presentation-cta__button {
    left: 10%;
  }
}
.footer {
  width: 100%;
  min-height: 250px;
  background-color: var(--white);
  text-decoration: none;
}

.footer-container {
  max-width: 68.75rem;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  font-size: 0.9375rem;
  padding: 1.875rem 0 0;
}

.footer-block {
  flex-basis: 20%;
  margin: 0 1.875rem;
}

.footer-block.block-logo {
  align-self: center;
}

.footer-logo {
  width: 12.5rem;
  height: 9.375rem;
}

.block-list {
  padding-left: 0;
}

.block-title {
  font-size: 1.125rem;
  color: var(--violet-hocarre);
}

.block-title::after {
  content: "";
  display: block;
  margin-top: 0.3125rem;
  width: 5rem;
  height: 0.125rem;
  background-color: var(--violet-hocarre);
}

.block-title.company::after {
  content: "";
  display: block;
  margin-top: 0.3125rem;
  width: 3.125rem;
  height: 0.125rem;
  background-color: var(--violet-hocarre);
}

.block-list__item {
  padding: 0.375rem 0;
}

.block-list.media {
  display: flex;
}

.block-list.media .block-list__item {
  padding-right: 0.9375rem;
  font-size: 1.375rem;
  transition: all 0.3s ease;
}

.block-list.media .block-list__item > a > i {
  color: var(--violet-hocarre);
}

.block-list.media .block-list__item:hover {
  transform: scale(1.22);
}

.block-list__item > a {
  color: rgba(51, 51, 51, 0.911);
}

/* ===> RESPONSIVE*/
@media screen and (max-width: 62.5rem) {
  .footer-container {
    max-width: 100%;
    flex-wrap: wrap;
  }

  .footer-block {
    flex-basis: auto;
    margin: 1.25rem 1.25rem;
  }
}
@media screen and (max-width: 37.5rem) {
  .footer-container {
    justify-content: center;
    padding-top: 1.875rem;
  }

  .footer-block {
    flex-basis: 70%;
  }
}